.DanmarkTesten                  { font-family: sans-serif; }

.DanmarkTesten .Status          { display: -ms-flexbox; display: flex;           }
.DanmarkTesten .Status li       { margin-right: 40px;      }
.DanmarkTesten .Status .seen    { color: green;            }
.DanmarkTesten .Status .active  { color: blue;             }
.DanmarkTesten .Status .final   { font-size: 20px;         }

.DanmarkTesten .TestStep        { text-align: center;      }
.DanmarkTesten .TestStep button { margin-bottom: 20px;     }

.DanmarkTesten button.active    { background-color: grey;  }



